import React from "react";

import Back from "../Back.js";
import immunoblotTemp from "../pics/immunoblotTemp.png";

class ImmunoblotExercise extends React.Component {
  render() {
    return (
      <>
        <div>
          <Back func={() => document.getElementById("Confirm").click()} />
        </div>
        <br />
        <h1>The lab has returned this image.</h1>
        <br />
        <div style={{ display: "flex", alignItems: "center", height: "500px" }}>
          <img
            src={immunoblotTemp}
            alt="Immunoblot test result"
            width="414px"
            height="282px"
            style={{ paddingRight: "70px" }}
          />

          <select
            value={this.props.passed_data.immunoblotAnswer}
            onChange={this.props.handleChange}
            name="immunoblotAnswer"
            disabled={this.props.passed_data.immunoblotDone ? true : false}
            style={{ fontSize: "18px", fontFamily: "Geneva" }}
          >
            <option value="">-- Please Choose a condition --</option>
            <option value="Immunoblot positive to NCC">
              Immunoblot positive to NCC
            </option>
            <option value="Immunoblot negative to NCC">
              Immunoblot negative to NCC
            </option>
          </select>

          <button
            onClick={() => {
              this.props.func(this.props.passed_data.immunoblotAnswer);
              document.getElementById("Home").click();
            }}
            disabled={
              this.props.passed_data.immunoblotDone
                ? true
                : this.props.passed_data.immunoblotAnswer === ""
                ? true
                : false
            }
            style={{ fontSize: "18px", fontFamily: "Geneva" }}
          >
            Submit
          </button>
        </div>
      </>
    );
  }
}

export default ImmunoblotExercise;
